/* Layout general */
body, .app {
  background: #0c0c0c;
  color: #fff;
  font-family: 'Segoe UI', sans-serif;
  margin: 0;
  padding: 0;
}
/* HEADER */
.header-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #000000;
  padding: 18px 20px;
  height: auto;
}

.logo {
  height: 42px;
  object-fit: contain;
}

.tagline {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 15px;
  color: #f5d37c;
  white-space: nowrap;
}

.divider {
  width: 2px;
  height: 28px;
  background-color: #f5d37c;
}

/* NAVBAR (misma estructura original) */
.nav-bar {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background: #1a1a1a;
  padding: 6px 4px;
  gap: 10px;
  font-size: 12px;
}

.nav-bar button {
  background: none;
  color: #aaa;
  border: none;
  font-size: 13px;
  padding: 4px 6px;
  cursor: pointer;
}

.nav-bar button.active {
  color: #f15454;
  font-weight: 600;
}

/* Títulos */
.recommendations h2,
.section h2 {
  padding: 14px 16px 8px;
  font-size: 18px;
  font-weight: 600;
  display: flex;
  align-items: center;
  color: #ffffff;
  gap: 8px;
}

/* RECOMENDACIONES */
.recommendations {
  position: relative;
  background-color: #0c0c0c;
  overflow: hidden;
  height: 90vh;
}
.recommendations::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background:
    linear-gradient(to top, transparent, rgba(0,0,0,0.8), #0c0c0c),
    url('../public/assets/bg-image2.jpg') no-repeat bottom center / cover;
  pointer-events: none;
  z-index: 0;
}
.recommendations > * {
  position: relative;
  z-index: 1;
}

/* CARD DE PLATOS */
.dish-card {
  display: flex;
  background: #1a1a1a;
  margin: 10px 16px;
  padding: 10px 12px;
  border-radius: 14px;
  gap: 12px;
  align-items: center;
}
.dish-card img {
  width: 58px;
  height: 100%;
  border-radius: 10px;
  object-fit: cover;
}
.card-content {
  flex: 1;
}
.card-content h4 {
  margin: 0;
  color: #f5d37c;
  font-size: 15.5px;
}
.card-content p {
  margin: 4px 0;
  color: #ccc;
  font-size: 13.5px;
  line-height: 1.3;
}
.card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 6px;
}
.price {
  font-size: 14px;
  color: #aaa;
}

/* EMOTIVO */
p {
  margin-top: 80%;
  margin: 10px 16px;
}
.emotive-quote {
  text-align: center;
  margin-top: 12px;
  font-style: italic;
  color: #777;
  font-size: 13px;
}

/* BOTÓN AR */
.ar-btn {
  background: #f15454;
  color: white;
  padding: 5px 10px;
  font-size: 11px;
  border-radius: 20px;
  border: none;
  font-weight: 600;
  cursor: pointer;
}
.ar-btn:hover {
  background: #d84242;
}

/* SUBSECCIONES */
.subsection h3 {
  color: #f5d37c;
  margin: 18px 16px 8px;
  font-size: 16px;
}

/* ITEM NORMAL */
.dish-item {
  display: flex;
  justify-content: space-between;
  background: #1a1a1a;
  margin: 8px 16px;
  padding: 12px;
  border-radius: 14px;
  align-items: center;
}
.dish-item img {
  width: 50px;
  height: 50px;
  border-radius: 8px;
  object-fit: cover;
  margin-right: 12px;
}
.dish-info {
  flex: 1;
}
.dish-info h4 {
  color: #f5f5f5;
  margin: 0;
  font-size: 14.5px;
}
.dish-info p {
  margin: 2px 0 0;
  color: #ccc;
  font-size: 13px;
}
